'use strict';
import * as angular from 'angular';
import { Store } from 'rdfstore';
import { EMap, StringSet } from '../components/collection-utils';
export class SparqlServiceDefinition {
    constructor(type, id) {
        this.type = type;
        this.id = id;
    }
}
export class FibraSparqlService {
    /* @ngInject */
    constructor(sparqlService, $q) {
        this.sparqlService = sparqlService;
        this.$q = $q;
        this.services = new EMap((id) => {
            if (id.indexOf('local:') === 0) {
                let r = this.$q.defer();
                new Store({ name: id, persistent: true }, (err, rdfStore) => {
                    if (err)
                        r.reject(err);
                    else
                        r.resolve(new RDFStoreInternalSparqlService(rdfStore, this.$q));
                });
                return r.promise;
            }
            else
                return this.$q.resolve(new RemoteInternalSparqlService(this.sparqlService, id));
        });
    }
    query(id, query, params) {
        return this.services.goc(id).then(s => s.query(query, params));
    }
    update(id, query, params) {
        return this.services.goc(id).then(s => s.update(query, params));
    }
    get(id, graphIRI, params) {
        return this.services.goc(id).then(s => s.get(graphIRI, params));
    }
    put(id, data, graphIRI, params) {
        return this.services.goc(id).then(s => s.put(data, graphIRI, params));
    }
    post(id, data, graphIRI, params) {
        return this.services.goc(id).then(s => s.post(data, graphIRI, params));
    }
}
class RemoteInternalSparqlService {
    constructor(s, endpoint) {
        this.s = s;
        this.endpoint = endpoint;
    }
    query(query, params) {
        return this.s.query(this.endpoint, query, params).then(s => s.data);
    }
    update(query, params) {
        return this.s.update(this.endpoint, query, params).then(s => s.data);
    }
    get(graphIRI, params) {
        return this.s.get(this.endpoint, graphIRI, params).then(s => s.data);
    }
    post(data, graphIRI, params) {
        return this.s.post(this.endpoint, data, graphIRI, params).then(s => s.data);
    }
    put(data, graphIRI, params) {
        return this.s.put(this.endpoint, data, graphIRI, params).then(s => s.data);
    }
}
export class RDFStoreInternalSparqlService {
    constructor(s, $q) {
        this.s = s;
        this.$q = $q;
    }
    static convertBinding(binding) {
        switch (binding.token) {
            case 'uri': return {
                type: 'uri',
                value: binding.value
            };
            case 'blank': return {
                type: 'bnode',
                value: binding.value
            };
            case 'literal': return {
                type: 'literal',
                value: binding.value,
                'xml:lang': binding.lang,
                datatype: binding.type
            };
            default: throw 'Unknown binding type ' + binding.token;
        }
    }
    query(query, params) {
        let deferred = this.$q.defer();
        try {
            this.s.execute(query, (err, results) => {
                if (err) {
                    console.log('Error executing', this.s, query, err);
                    deferred.reject(err);
                }
                else {
                    let ret = {
                        head: {
                            vars: []
                        },
                        results: {
                            bindings: []
                        }
                    };
                    let vars = new StringSet();
                    ret.results.bindings = results.map(binding => {
                        let tb = {};
                        for (let v in binding) {
                            vars.add(v);
                            if (binding[v] !== null)
                                tb[v] = RDFStoreInternalSparqlService.convertBinding(binding[v]);
                        }
                        return tb;
                    });
                    ret.head.vars = vars.values();
                    deferred.resolve(ret);
                }
            });
        }
        catch (err) {
            console.log('Exception executing', this.s, query, err);
            throw err;
        }
        return deferred.promise;
    }
    update(query, params) {
        let deferred = this.$q.defer();
        this.s.execute(query, (err, results) => {
            if (err)
                deferred.reject(err);
            else
                deferred.resolve(results);
        });
        return deferred.promise;
    }
    get(graphIRI, params) {
        let deferred = this.$q.defer();
        this.s.graph(graphIRI, (err, graph) => {
            if (err)
                deferred.reject(err);
            else
                deferred.resolve(graph.toNT());
        });
        return deferred.promise;
    }
    post(data, graphIRI, params) {
        let deferred = this.$q.defer();
        let handler = (err, graph) => {
            if (err)
                deferred.reject(err);
            else
                deferred.resolve();
        };
        if (graphIRI)
            this.s.load('text/turtle', data, graphIRI, handler);
        else
            this.s.load('text/turtle', data, handler);
        return deferred.promise;
    }
    put(data, graphIRI, params) {
        let deferred = this.$q.defer();
        let handler = (err) => {
            if (err)
                deferred.reject(err);
            let handler2 = (err2, graph) => {
                if (err2)
                    deferred.reject(err2);
                else
                    deferred.resolve();
            };
            if (graphIRI)
                this.s.load('text/turtle', data, graphIRI, handler2);
            else
                this.s.load('text/turtle', data, handler2);
        };
        if (graphIRI)
            this.s.clear(graphIRI, handler);
        else
            this.s.clear(handler);
        return deferred.promise;
    }
}
angular.module('fibra.services.fibra-sparql-service', [])
    .config(($provide) => {
    $provide.service('fibraSparqlService', FibraSparqlService);
});



