'use strict';
import { SKOS, DCTerms, FOAF, FIBRA, RDF, XMLSchema, ONodeSet } from 'models/rdf';
export class CitableSource {
    constructor(sparqlEndpoint, graph) {
        this.sparqlEndpoint = sparqlEndpoint;
        this.graph = graph;
    }
}
export class Citable {
    constructor(id, source = new CitableSource(null), labels = new ONodeSet(), url, descriptions = new ONodeSet(), rightsHolders = [], dateCreated = new Date()) {
        this.source = source;
        this.labels = labels;
        this.url = url;
        this.descriptions = descriptions;
        this.rightsHolders = rightsHolders;
        this.dateCreated = dateCreated;
        this.__className = 'Citable';
        this.id = id;
        this.source = source;
    }
    static toTurtle(c, tb) {
        tb.prefixes['skos'] = SKOS.ns;
        tb.prefixes['dcterms'] = DCTerms.ns;
        tb.prefixes['foaf'] = FOAF.ns;
        tb.prefixes['fibra'] = FIBRA.ns;
        tb.prefixes['rdf'] = RDF.ns;
        tb.prefixes['xsd'] = XMLSchema.ns;
        let f = tb.fragmentsById.get(c.id);
        c.labels.each(label => {
            if (label.value)
                f = f + `
skos:prefLabel ${label.toCanonical()} ;`;
        });
        f = f + `
dcterms:created "${c.dateCreated.toISOString()}"^^xsd:dateTime ;`;
        c.descriptions.each(descr => {
            if (descr.value)
                f = f + `
dcterms:description ${descr.toCanonical()} ;`;
        });
        if (c.url)
            f = f + `
foaf:homepage <${c.url}> ;`;
        if (c.rightsHolders.length > 0) {
            f = f + `
dcterms:rightsHolder `;
            c.rightsHolders.forEach(rh => {
                f = f + `<${rh.id}>, `;
                if (!tb.fragmentsById.has(rh.id)) {
                    tb.fragmentsById.set(rh.id, `<${rh.id}> a foaf:Agent ;`);
                    rh.toTurtle(tb);
                }
            });
            f = f.substring(0, f.length - 2) + ' ;';
            if (c.rightsHolders.length > 1)
                c.rightsHolders.forEach((rh, index) => {
                    f = f + `
fibra:qualifiedAssertion [
  rdf:predicate dcterms:rightsHolder ;
  rdf:object <${rh.id}> ;
  fibra:order ${index} ;
] ;`;
                });
        }
        tb.fragmentsById.set(c.id, f);
    }
    toTurtle(tb) { Citable.toTurtle(this, tb); }
}



