'use strict';
import * as angular from 'angular';
let uriCache = new Map();
export function getPrefLangString(literals, prefLang, uri) {
    if (uri && uriCache.has(uri)) {
        // console.log('hit')
        return uriCache.get(uri);
    }
    if (!literals) {
        if (uri) {
            let lname = uri.replace(/.*\//, '').replace(/.*\#/, '').replace('_', ' ').replace(/[A-Z]/g, (match) => ' ' + match.toLocaleLowerCase());
            if (lname !== '')
                return lname;
            return uri;
        }
        return '?';
    }
    let dl = null;
    let pl = null;
    let al = null;
    let plParts = prefLang.split('-');
    let p = l => {
        if (l.language === '')
            dl = l.value;
        else
            al = l.value;
        if (l.language === prefLang)
            return true;
        let lparts = l.language.split('-');
        if (lparts.length >= plParts.length && plParts.every((value, index) => value === lparts[index]))
            pl = l.value;
        return false;
    };
    let cl = literals instanceof Array ? literals.find(p) : literals.find(p);
    if (cl) {
        if (uri)
            uriCache.set(uri, cl.value);
        return cl.value;
    }
    if (pl)
        return pl;
    if (dl)
        return dl;
    if (al)
        return al;
    if (uri) {
        let lname = uri.replace(/.*\//, '').replace(/.*\#/, '').replace('_', ' ');
        lname = lname[0] + lname.substring(1).replace(/[A-Z]/g, (match) => ' ' + match.toLocaleLowerCase());
        if (lname !== '')
            return lname;
        return uri;
    }
    return '?';
}
angular.module('fibra.filters.pref-lang-label', [])
    .filter('prefLangLabel', /* @ngInject */ ($ngRedux) => (node) => {
    if (!node)
        return '?';
    return getPrefLangString(node.labels, $ngRedux.getState().general.language, node.value);
});
angular.module('fibra.filters.pref-lang', [])
    .filter('prefLang', /* @ngInject */ ($ngRedux) => (literals, uri) => {
    return getPrefLangString(literals, $ngRedux.getState().general.language, uri);
});



