'use strict';
import { EMap, StringSet } from 'components/collection-utils';
export class UnifyMap {
    constructor() {
        this.idToIdSet = new EMap(() => new StringSet()); // id to sameAs ids
        this.ifpVarPlusValueToIdSet = new EMap(() => new EMap(() => new StringSet())); // inverse functional properties & values to ids
    }
    consolidate(result, idVar = 'id', sameAsVar = 'sameAs', ifpVars = result.head.vars.filter(v => v.indexOf('ifp') === 0)) {
        result.results.bindings.forEach(binding => {
            let id = binding[idVar].value;
            this.idToIdSet.goc(id).add(id);
            if (binding[sameAsVar]) {
                this.idToIdSet.get(id).add(binding[sameAsVar].value);
                this.idToIdSet.goc(binding[sameAsVar].value).add(id);
            }
            for (let v of ifpVars)
                if (binding[v])
                    this.ifpVarPlusValueToIdSet.goc(v).goc(binding[v].value).add(id);
        });
        // create sameAses for all objects sharing the same inverse functional property values
        this.ifpVarPlusValueToIdSet.each(valueToIdSet => valueToIdSet.each(ids => ids.each(id2 => this.idToIdSet.goc(id2).adds(ids))));
        // consolidate id sets
        this.idToIdSet.each((idSet) => idSet.each(id => {
            let oidSet = this.idToIdSet.get(id);
            if (idSet !== oidSet) {
                this.idToIdSet.set(id, idSet);
                idSet.adds(oidSet);
            }
        }));
    }
}



