'use strict';
import { FMap } from './collection-utils';
export function toTurtle(tb) {
    let s = '';
    for (let key in tb.prefixes)
        s = s + '@prefix ' + key + ': <' + tb.prefixes[key] + '> .\n';
    tb.fragmentsById.values().forEach(str => s = s + str.substring(0, str.length - 2) + ' .\n\n');
    return s;
}
export class TurtleBuilder {
    constructor() {
        this.prefixes = {};
        this.fragmentsById = new FMap();
    }
}
export function citableToTurtle(c) {
    let tb = new TurtleBuilder();
    c.toTurtle(tb);
    return toTurtle(tb);
}
let lut = [];
for (let i = 0; i < 256; i++)
    lut[i] = (i < 16 ? '0' : '') + i.toString(16);
export function UUID() {
    /* tslint:disable:no-bitwise */
    let d0 = Math.random() * 0xffffffff | 0;
    let d1 = Math.random() * 0xffffffff | 0;
    let d2 = Math.random() * 0xffffffff | 0;
    let d3 = Math.random() * 0xffffffff | 0;
    return lut[d0 & 0xff] + lut[d0 >> 8 & 0xff] + lut[d0 >> 16 & 0xff] + lut[d0 >> 24 & 0xff] + '-' +
        lut[d1 & 0xff] + lut[d1 >> 8 & 0xff] + '-' + lut[d1 >> 16 & 0x0f | 0x40] + lut[d1 >> 24 & 0xff] + '-' +
        lut[d2 & 0x3f | 0x80] + lut[d2 >> 8 & 0xff] + '-' + lut[d2 >> 16 & 0xff] + lut[d2 >> 24 & 0xff] +
        lut[d3 & 0xff] + lut[d3 >> 8 & 0xff] + lut[d3 >> 16 & 0xff] + lut[d3 >> 24 & 0xff];
    /* tslint:enable:no-bitwise */
}
export function flatten(arr, initial = []) {
    return arr.reduce((flat, toFlatten) => flat.concat(Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten), initial);
}



