'use strict';
import { ONodeSet } from '../../models/rdf';
import { FullRichNodeFromNode } from '../../models/richnode';
import { FMap } from '../../components/collection-utils';
export class Class extends FullRichNodeFromNode {
    constructor(id) {
        super(id);
        this.superClasses = new ONodeSet();
        this.subClasses = new ONodeSet();
        this.properties = new ONodeSet();
        this.inverseProperties = new ONodeSet();
    }
}
export class Property extends FullRichNodeFromNode {
    constructor(id) {
        super(id);
        this.domains = new ONodeSet();
        this.ranges = new ONodeSet();
        this.superProperties = new ONodeSet();
        this.subProperties = new ONodeSet();
    }
}
export class DataModel {
    constructor() {
        this.classMap = new FMap();
        this.propertyMap = new FMap();
        this.rootClasses = [];
        this.rootProperties = [];
    }
    static getFilter(types) {
        if (types.length === 0)
            return '';
        else
            return 'FILTER (?groupId IN (' + types.map(id => id.toCanonical()).join(', ') + '))';
    }
}
DataModel.propertyQuery = `PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
PREFIX skos: <http://www.w3.org/2004/02/skos/core#>
PREFIX owl: <http://www.w3.org/2002/07/owl#>
PREFIX sf: <http://ldf.fi/functions#>
PREFIX dcterms: <http://purl.org/dc/terms/>
PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
SELECT ?id ?domains ?ranges ?labels ?descriptions ?types ?inverseProperty ?superProperties ?subProperties {
# STARTGRAPH
  {
    ?types rdfs:subClassOf* rdf:Property .
    ?id a ?types .
  }
    {
    ?id skos:prefLabel|rdfs:label ?labels .
  } UNION {
    ?id rdfs:comment|dcterms:description ?descriptions .
  } UNION {
    ?id owl:inverseOf ?inverseProperty .
  } UNION {
    ?id rdfs:domain ?domains .
  } UNION {
    ?id rdfs:range ?ranges .
  } UNION {
    ?id rdfs:subPropertyOf ?superProperties .
  } UNION {
    ?subProperties rdfs:subPropertyOf ?id .
  }
# ENDGRAPH
}`;
DataModel.classQuery = `PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
PREFIX skos: <http://www.w3.org/2004/02/skos/core#>
PREFIX owl: <http://www.w3.org/2002/07/owl#>
PREFIX sf: <http://ldf.fi/functions#>
PREFIX dcterms: <http://purl.org/dc/terms/>
SELECT ?id ?types ?labels ?descriptions ?superClasses ?subClasses {
# STARTGRAPH
  {
    ?types rdfs:subClassOf* rdfs:Class .
    ?id a ?types .
  }
  {
    ?id skos:prefLabel|rdfs:label ?labels .
  } UNION {
    ?id rdfs:comment|dcterms:description ?descriptions .
  } UNION {
    ?id rdfs:subClassOf ?superClasses .
  } UNION {
    ?subClasses rdfs:subClassOf ?id .
  }
# ENDGRAPH
}`;



